import { useEffect, useState } from "react"
import { useDispatch, useSelector } from "react-redux";
import {AddFeedbackAction} from '../../actions/feedbackActions'
import cypherlogo from "../../bgAndLogos/cypherlogo.gif"

import Header from "../../components/headers"
import { AlphabetValidate, BetweenFixLengthFeedbackdata, Emailvalidater, validate } from "../utils/utils";
import { useHistory } from 'react-router-dom'
const AddFeedback = (props) =>{
    const [name,setName]=useState('');
    const [email,setEmail]=useState('');
    const [Feedback,setFeedback]=useState('');

    const dispatch = useDispatch()
    const addfeedbackdata = useSelector((store)=>store.addfeedback)
    const {error,response,loading} = addfeedbackdata




const onSubmit =()=>{

if(validate('name','nameDiv','This field is mandatory..')&&
    AlphabetValidate('name','nameDiv','Plese enter valid name')&&
    validate('email','emailDiv','Please enter your Email')&&
    validate('feedback','feedbackDiv','This field should not be Empty')&&
    BetweenFixLengthFeedbackdata('feedback','feedbackDiv','Enter your feedback in between 4 to 500 words')&&
    Emailvalidater('email','emailDiv','Please Enter a valid Email')
){
  console.log("i m here")
  dispatch(AddFeedbackAction(name,email,Feedback))
}



}
const history = useHistory()
useEffect(()=>{
  if(response && response.data.status === "success"){
    sessionStorage.setItem('token',response.data.token)
    history.push('/successfeedback')
  }else if(response && response.data.status === "error"){
    alert(response.error)
  }else if(error){
alert(error)
  }
})
const onCancel =()=>{
    history.push('/home')
}

    return(
        <div>


            <div className="container">
      
      <div className="row">
      
        <div className="col-md-8 offset-md-2">
        <div className="boxforcontacts-contact">
          <div className="login-form">

          <div className="form-group">
          <Header title="we are here to assist you in anyway possible !"/>

                <label className="Names" >Enter Your Name</label>
                <input id="name" onChange={(e)=>{
                    setName(e.target.value)
                }} type="text" className="boxforcontacts form-control"   placeholder="Enter Your full name Name"/>
                <div id="nameDiv" className="errordiv" ></div>
              </div>
             
              <div className="form-group">
                <label className="Names" >Email address</label>
                <input id="email" onChange={(e)=>{
                    setEmail(e.target.value)
                }} type="email" className="boxforcontacts form-control"   placeholder="Enter email"/>
                <div className="errordiv" id="emailDiv"  ></div>
              </div>
              
              <div className="form-group">
                <label className="Names" >Message</label>
                <input id="feedback" onChange={(e)=>{
                    setFeedback(e.target.value)
                }} type="text" className="boxforcontacts addressbox form-control"   placeholder="Enter your Message/ Queries "/>
                <div className="errordiv" id="feedbackDiv" ></div>
              </div>
              
             
         
              <button onClick={onSubmit} type="submit" className="btn btn-primary">Submit</button>
              <button onClick={onCancel} className="btn btn-danger float-end">Cancel</button>
         <div className="float-end">
</div>
</div>
</div>
</div>
          </div>
        </div>
        </div>
    )
}

export default AddFeedback