import { useDispatch, useSelector } from "react-redux"
import Header from "../../components/headers"
import blog from "../../bgAndLogos/blog.jpg"
import contact from "../../bgAndLogos/contact.jpg"
import { useHistory } from "react-router-dom"

const DashBoard =(props)=>{
const history = useHistory();
    const dispatch = useDispatch();
const session = sessionStorage.getItem("token")
const signin = useSelector((store)=>store.signin)
console.log(session)
    const blogger =()=>{
       props.history.push('/blog')
            }
     const ShowContactbook =()=>{
      props.history.push('/contacts')
                     }
return(
    <div>
        
{ session != null ? <div>
        <Header title="Dashboard"/>



        <div >

<img src={blog} onClick={blogger} className="blog"></img>  
      </div>

      <div  >

<img src={contact} onClick={ShowContactbook} className="blog"></img>  
      </div>
      <div >

<img  className="blog"></img>  
      </div>

    </div>

:history.push("/unauthorisedAccess")}</div>
)

}

export default DashBoard