import axios from "axios"
import { ADD_BLOG_FAIL, ADD_BLOG_REQUEST, ADD_BLOG_SUCCESS, GET_BLOG_FAIL, GET_BLOG_REQUEST, GET_BLOG_SUCCESS } from "../constants/blogConstants"

export const AddBlogAction=(email,blog,title)=>{
return (dispatch)=>{
        dispatch({
            type:ADD_BLOG_REQUEST,
        })
        const header = {
            headers:({
                'content-type':'application/json'
            })
        }

        const url="http://localhost/:2021/addblog"

        const body = {        
        email,
        blog,
        title
        }

        axios.post(url,body,header)
              .then((response)=>{
              
                    dispatch({
                        type:ADD_BLOG_SUCCESS,
                        payload:response
                    })
               
              })
            
              .catch((error)=>{
                  dispatch({
                      type:ADD_BLOG_FAIL,
                      payload:error
                    
                  })
              
              })



        
}
}

export const GetBlogAction=()=>{
return (dispatch)=>{
      dispatch({
          type:GET_BLOG_REQUEST
      })
      const header = {
        headers:({
            'content-type':'application/json'
        })
    }

    const url="http://localhost:2021/getblog"


    axios.get(url,header)
         .then((response)=>{
          
                dispatch({
                    type:GET_BLOG_SUCCESS,
                    payload:response
                })

               
                       })
        
          .catch((error)=>{
              dispatch({
                  type:GET_BLOG_FAIL,
                  payload:error
                
              })
          
          })


}
}