import { useDispatch, useSelector } from "react-redux"
import cypherlogo from "../bgAndLogos/cypherlogo.gif"
import {logout} from "../actions/useractions"


const { Link } = require("react-router-dom")

const Navigation = (props)=>{
const dispatch = useDispatch();
const session = sessionStorage.getItem("token")
  const sigin = useSelector((store)=>store.signin);

  const onlogout =()=>{
    dispatch(logout())
        }

    return(
      <div >
        
    <nav className="navbar navbar-expand-lg navbar-light bg-light">
  <div  className="container-fluid">
     <Link to="/home" className="navbar-brand">     <img src={cypherlogo} className="logo"></img>
</Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
      
    <li className="nav-item">
      <Link to="/home" className="nav-link">Home</Link>
    </li>
         
    <li className="nav-item">
      <Link to="/about" className="nav-link">About</Link>
    </li>    
    <li className="nav-item">
      <Link to="/contactus" className="nav-link">Contact Us</Link>
    </li>
            
    <li className="nav-item">
      <Link to="/careers" className="nav-link">Careers</Link>
    </li>

    <li className="nav-item">
      <Link to="/addfeedback" className="nav-link">Feedback</Link>
    </li>
    
    <li >
      
    {session != null &&
         <Link className= "nav-link logout" onClick={onlogout}>Logout</Link>
  }
    </li>

      </ul>
    
    </div>
  </div>
</nav>
            </div>
    )
     }
    
    
    
     export default Navigation