import { useEffect } from "react"
import { useDispatch, useSelector } from "react-redux"
import Header from "../../components/headers"
import {GetFeedbackAction} from "../../actions/feedbackActions"
const GetFeedback = () =>{
    const dispatch = useDispatch()

    const users = useSelector((store)=> store.getfeedback)

    const {error,response,loading} =users

    useEffect(()=>{
        dispatch(GetFeedbackAction())
    },[])

    useEffect(()=>{

    },[error,response,loading])


  


   



 
  
    return(
        <div>
<Header title="Home"/>


<table className="table table-dark table-hover">
    <thead>
        <tr>
            <td>id</td>
            <td>Name</td>
            <td>Email</td>
             <td>Message</td>
             <td>Date</td>
        </tr>
    </thead>
    <tbody>
        {response &&
        response.data &&
        response.data.data &&
        response.data.data.length > 0 &&
        response.data.data.map((e)=>{
            return(
                <tr>
                    <td >{e.id}</td>
                    <td>{e.fname }</td>
                                      
                   <td>{e.email} </td>

                    <td>{e.feedback}</td>
                    <td>{e.date}</td>


                </tr>
            )
        })
            
        }
    </tbody>
</table>


        </div>

        
    )
} 



export default GetFeedback