import { ADD_BLOG_FAIL, ADD_BLOG_REQUEST, ADD_BLOG_SUCCESS, GET_BLOG_FAIL, GET_BLOG_REQUEST, GET_BLOG_SUCCESS } from "../constants/blogConstants"

export const GetBlogReducer = (state={},action)=>{

    switch(action.type){
        case GET_BLOG_REQUEST:
        return {loading:true }

        case GET_BLOG_SUCCESS:
            return {loading:true, response:action.payload}

        case GET_BLOG_FAIL:
            return {loading:false, error:action.payload}

        default:
            return state
    }

}

export const AddBlogReducer = (state={},action)=>{

    switch(action.type){
        case ADD_BLOG_REQUEST:
        return {loading:true }

        case ADD_BLOG_SUCCESS:
            return {loading:false, response:action.payload}

        case ADD_BLOG_FAIL:
            return {loading:false, error:action.payload}

        default:
            return state
    }

}


