import {addcontactbook} from '../../actions/contactbooActions'
import  Validator from 'validator'
import { useState} from 'react'
import {useDispatch} from 'react-redux'
import signupbg from "../../bgAndLogos/contactbg.jpg"
import { useHistory } from 'react-router-dom'
import { AlphabetValidate, fixedLengthNumbers, NumberValidate } from '../utils/utils'
import UnauthorisedAccess from '../blog/unautherised'

const { default: Header } = require("../../components/headers")

const AddContactScreen = (props)=>{
const history =useHistory()

    const [fname,setFname]=useState('');
    const [mname,setMname]=useState('');
    const [lname,setLname]=useState('');
    const [email,setEmail]=useState('');
    const [phone,setPhone]=useState('');
    const [alterphone,setAlterphone]=useState('');
    const [address,setAddress]=useState('');

const dispatch = useDispatch()


function validate(IDofInput,IDofErrordiv,errormsg){
const IDofInpu = document.getElementById(IDofInput)
const errorDiv = document.getElementById(IDofErrordiv)


if(IDofInpu.value !== ''){
  errorDiv.innerHTML=''
  return true
}
else{
  errorDiv.innerHTML = errormsg
  return false
}
}



 
const onAdd=()=>{
   if(validate('firstname','firstnameDiv','First name is mandatory....') &&
    AlphabetValidate('firstname','firstnameDiv','No person in world whos First name has numbers....')&&
    AlphabetValidate('lastname','lastnameDiv','No person in world whos last name has numbers...!')&&
     validate('lastname','lastnameDiv','Last naem is mandatory...!')&&
     validate('email','emailDiv','Email is mandatory...!')&&
     validate('phone','phoneDiv','Phone is manadatory')&&
     NumberValidate('phone','phoneDiv','Your phone will never ring with alphabets or specials..')&&
     fixedLengthNumbers(phone,'phoneDiv','Only 10 Digit Numbers Allowd..')&&
     validate('address','addressDiv','Address is required')
  ){
 
 const emailverify = document.getElementById('emailDiv')
 if (!Validator.isEmail(email))
  {
    emailverify.innerHTML = 'enter valid email'
    return true

  } 
      
  dispatch(addcontactbook(fname,mname,lname,email,phone,alterphone ,address))
   
 }
   
   
    
  
}
const session = sessionStorage.getItem("token")

const onCancel=()=>{
    history.push('/contacts')
  }
  
    return(
      <div >
          {session != null ? 
    <div className="container">
      
      <div className="row">
      
        <div className="col-md-8 offset-md-2">
        <div className="boxforcontacts-contact">
          <div className="login-form">
          <Header title="Add contact"></Header>
          <div className="form-group">
                <label className="Names" >First Name</label>
                <input id="firstname" onChange={(e)=>{
                    setFname(e.target.value)
                }} type="text" className="boxforcontacts form-control"   placeholder="Enter Your First Name"/>
                <div id="firstnameDiv" className="errordiv" ></div>
              </div>
              <div className="form-group">
                <label className="Names" >Middle Name</label>
                <input id="mname" onChange={(e)=>{
                    setMname(e.target.value)
                }} type="text" className="boxforcontacts form-control"   placeholder="Enter Your First Name"/>
                <div id="mnameDiv" className="errordiv" ></div>
              </div>
              
              <div className="form-group">
                <label className="Names" >Last Name</label>
                <input id="lastname" onChange={(e)=>{
                    setLname(e.target.value)
                }} type="text" className="boxforcontacts form-control"   placeholder="Enter Your Last Name"/>
                <div className="errordiv" id="lastnameDiv"  ></div>
              </div>
              <div className="form-group">
                <label className="Names" >Email address</label>
                <input id="email" onChange={(e)=>{
                    setEmail(e.target.value)
                }} type="email" className="boxforcontacts form-control"   placeholder="Enter email"/>
                <div className="errordiv" id="emailDiv"  ></div>
              </div>
              <div className="form-group">
                <label className="Names" >Phone</label>
                <input id="phone" onChange={(e)=>{
                    setPhone(e.target.value)
                }} type="phone" className="boxforcontacts form-control"   placeholder="Enter Your Phone number with country code"/>
                <div  className="errordiv" id="phoneDiv" ></div>
              </div>
              <div className="form-group">
                <label className="Names" >Alternate Phone</label>
                <input id="phone" onChange={(e)=>{
                    setAlterphone(e.target.value)
                }} type="phone" className="boxforcontacts form-control"   placeholder="Enter Your Phone number with country code"/>
                <div  className="errordiv" id="alternatephoneDiv" ></div>
              </div>
              <div className="form-group">
                <label className="Names" >Address</label>
                <input id="address" onChange={(e)=>{
                    setAddress(e.target.value)
                }} type="text" className="boxforcontacts form-control"   placeholder="Enter Your Address : city/town, tal, dist, state, country "/>
                <div className="errordiv" id="addressDiv" ></div>
              </div>
              
             
              <div className="form-group form-check">
                <input type="checkbox" className="form-check-input" />
                <label className="form-check-label" >Check me out</label>
              </div>
              <button onClick={onAdd} type="submit" className="btn btn-primary">Submit</button>
              <button onClick={onCancel} className="btn btn-danger float-end">Cancel</button>
         <div className="float-end">
</div>
</div>
          </div>
        </div>
      </div>
    </div>

              :<UnauthorisedAccess/>}
  
 
  
</div>





       
    )
     }
    
    
    
     export default AddContactScreen