import Header from "../../components/headers"
import {GetcontactAction} from "../../actions/contactbooActions"
import { useDispatch, useSelector } from "react-redux"
import { useEffect } from "react"
import { useHistory } from "react-router-dom"
import UnauthorisedAccess from "../blog/unautherised"
const ViewContacts=()=>{


    const dispatch = useDispatch()
    useEffect(()=>{
        dispatch(GetcontactAction())
    },[])

const ViewContacts = useSelector((store)=>store.showcontacts)
const {error,response,loading}=ViewContacts



useEffect(()=>{

},[error,response,loading])
const history = useHistory()
const Addcontact =()=>{
history.push('/addcontact')
}
const session = sessionStorage.getItem("token")


    return(
        <div>
        {session != null ? <div>
            <Header title="Contacts"/>


<table className="table table-stripped">
    <thead>
        <tr >
            <td className="table-primary">id</td> 
           
            <td className="table-secondary">Name</td>
            <td class="table-success">Contact Info</td>
            
        </tr>
    </thead>
    <tbody>
        {response &&
        response.data &&
        response.data.data &&
        response.data.data.length > 0 &&
        response.data.data.map((d)=>{
            return(
                <tr>
                    <td>{d.id}</td>
                    <td>{d.fname + " "+ d.mname + " " +d.lname}</td>
                    <td>Address:- {d.address}
                    <td>{ d.email}
                    <td>Number: {d.phone}
                    <td>Alternate number: {d.alterphone}</td>
                    </td>
                    </td></td>
                   
                </tr>
            )
        })
        }
    </tbody>
</table>
<button className="boxforcontacts btn btn-info" onClick={Addcontact}>Add Contact</button>
 </div>:<UnauthorisedAccess/>}
        </div>
    )
}


export default ViewContacts