import './App.css';
import {BrowserRouter as Router, Route,Switch,Redirect} from 'react-router-dom'
import HomeScreen from './screens/homeScreen';
import SigninScreen from './screens/userscreens/signinScreen';
import SignupScreen from './screens/userscreens/signupScreen';
import Navigation from './components/navigation';
import Showuser from './screens/showuser';
import ConfirmUser from './screens/userscreens/confirmuser';
import DashBoard from './screens/userscreens/dashboard';
import Blog from './screens/blog/blog';
import AddContactScreen from './screens/contactbook/addcontact';
import ViewContacts from './screens/contactbook/contactsView';
import AboutUsScreen from './screens/aboutus';
import CareersScreen from './screens/Careers';
import ContactUsScreen from './screens/contactus';
import AddFeedback from './screens/feedback/feedbackform';
import GetFeedback from './screens/feedback/getfeedback';
import SuccessFeedback from './screens/feedback/successfeedback';
import UnauthorisedAccess from './screens/blog/unautherised';
function App() {
  return (
    <Router>
      <div > 
      <Navigation/>
        <Switch>
         
            <Route path="/Home" component={HomeScreen}/>
            <Route path="/Signin" component={SigninScreen}/>
            <Route path="/signup" component={SignupScreen}/>
            <Route path="/viewusers" component={Showuser}/>
            <Route path="/confirm" component={ConfirmUser}/>
            <Route path="/dashboard" component={DashBoard}/>
            <Route path="/blog" component={Blog}/>
            <Route path="/addcontact" component={AddContactScreen}/>
            <Route path="/contacts" component={ViewContacts}/>
            <Route path="/about" component={AboutUsScreen}/>
            <Route path="/careers" component={CareersScreen}/>
            <Route path="/contactus" component={ContactUsScreen}/>
            <Route path="/addfeedback" component={AddFeedback}/>
            <Route path="/getfeedback" component={GetFeedback}/>
            <Route path="/successfeedback" component={SuccessFeedback}/>
            <Route path="/unauthorisedAccess" component={UnauthorisedAccess}/>
            <Route path="/"><Redirect to="/Home" /></Route>        
        </Switch>
    
      </div>
    </Router>
  );}

export default App;
