import axios from "axios"
import { ADD_FEEDBACK_FAIL, ADD_FEEDBACK_REQUEST, ADD_FEEDBACK_SUCCESS, GET_FEEDBACK_FAIL, GET_FEEDBACK_REQUEST, GET_FEEDBACK_SUCCESS } from "../constants/feedback"

export const AddFeedbackAction=(name,email,feedback)=>{
return (dispatch)=>{
        dispatch({
            type:ADD_FEEDBACK_REQUEST
        })
        const header = {
            headers:({
                'content-type':'application/json'
            })
        }

        const url="http://localhost:2021/addfeedback"

        const body = {
        name,
        email,
        feedback
        }

        axios.post(url,body,header)
              .then((response)=>{
              
                    dispatch({
                        type:ADD_FEEDBACK_SUCCESS,
                        payload:response
                    })
               
              })
            
              .catch((error)=>{
                  dispatch({
                      type:ADD_FEEDBACK_FAIL,
                      payload:error
                    
                  })
              
              })



        
}
}

export const GetFeedbackAction=()=>{
return (dispatch)=>{
      dispatch({
          type:GET_FEEDBACK_REQUEST
      })
      const header = {
        headers:({
            'content-type':'application/json'
        })
    }

    const url="http://localhost:2021/getfeedback"


    axios.get(url,header)
          .then((response)=>{
          
                dispatch({
                    type:GET_FEEDBACK_SUCCESS,
                    payload:response
                })

               
                       })
        
          .catch((error)=>{
              dispatch({
                  type:GET_FEEDBACK_FAIL,
                  payload:error
                
              })
          
          })


}
}