import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Header from "../../components/headers"
import useToggle from "./toggle";
import {GetBlogAction,AddBlogAction} from "../../actions/Blogactions"
import Collapsible from 'react-collapsible'

import { useHistory } from "react-router-dom";
import UnauthorisedAccess from "./unautherised";
const Blog =(props)=>{
   
const [isOn, toggleIsOn] = useToggle();


const dispatch=useDispatch();
const history = useHistory()
const [blogtitle,setBlogtitle]=useState('');
const [email,setEmail]=useState('');
const [blog,setBlog]=useState('');
const [unauther,setUnauther]=useState('');
const session = sessionStorage.getItem("token")

const blogdata = useSelector((store)=>store.getblog);
const {error,response,loading}=blogdata
useEffect(()=>{
    dispatch(GetBlogAction())
},[])
useEffect(()=>{
    if (response && response.data.status === 'success') {
      } 
             
        
      else if (error) {
        alert(error)
      }
},[error,response,loading]);



const onSubmit=()=>{
dispatch(AddBlogAction(email,blog,blogtitle))

toggleIsOn()
}


return(
<div>  
        
                    <div class="container-fluid">
                                                       <div class="row flex-nowrap">
                                                            <div class="col-auto bg-dark">
                                                                <div class="d-flex ">
   
                                                                    <ul class="nav nav-pills flex-column " id="menu">
                                                                        <li class="nav-item">
                                                                        <button onClick={toggleIsOn} href="#" class="nav-link align-middle px-0">
                                                                            <i class="fs-4 bi-house"></i> <span class="ms-1 d-none d-sm-inline">New Blog</span>
                                                                            </button>
                                                                            </li>

                                                                            <li>
                                                                                <a href="#submenu1" data-bs-toggle="collapse" class="nav-link px-0 align-middle">
                                                                                    <i class="fs-4 bi-speedometer2"></i> <span class="ms-1 d-none d-sm-inline">Dashboard</span> </a>
                                                                            
                                                                            </li>
                                                                            
                                                                        </ul>
              
               
                    
                                                                </div>
                                                            </div>
                                                                       
                                                                        
                                                                            <div class="col py-3">
            
            


                                                                                <div>
                                                    
                                                
                                                                                    
                                                                                        {isOn && <div>
                                                                                            
                                                                                
                                                                          
                                                                             {session != null ? 
                                                                                         <div className="container">
      
      
                                                                                            <div className="col-md-8 offset-md-2">
                                                                                                <div className="boxforcontacts-contact">
                                                                                                    <div className="login-form">

                                                                                                        <div className="form-group">
                                                                                                                 <Header title="Add your Blog"/>
         
                                                                                                            <label className="Names" >Blog Title</label>
                                                                                                            <input id="title" onChange={(e)=>{
                                                                                                                setBlogtitle(e.target.value)
                                                                                                            }} type="text" className="boxforcontacts form-control"   placeholder="Enter Your full name Name"/>
                                                                                                            <div id="blogtitleDiv" className="errordiv" ></div>
                                                                                                        </div>
                                                                                                    
                                                                                                        <div className="form-group">
                                                                                                               <label className="Names" >Your Email</label>
                                                                                                                 <input id="email" onChange={(e)=>{
                                                                                                            setEmail(e.target.value)
                                                                                                                }} type="email" className="boxforcontacts form-control"   placeholder="Enter email"/>
                                                                                                              <div className="errordiv" id="emailDiv"  ></div>
                                                                                                        </div>
                                                                                                                
                                                                                                    <div className="form-group">
                                                                                                        <label className="Names" >Your Blog</label>
                                                                                                        
                                                                                                        <textarea id="blog" onChange={(e)=>{
                                                                                                            setBlog(e.target.value)
                                                                                                        }} type="text" className="boxforcontacts blogbox scrollbox form-control"   placeholder="Enter your Message/ Queries "/>
                                                                                                        <div className="errordiv" id="blogkDiv" ></div>
                                                                                                    </div>
                                                                                                    
                      
                                                                                                        <button onClick={onSubmit} type="submit" className="blogbutton btn btn-primary">Submit</button>
                                                                                                        <button onClick={toggleIsOn} className="btn btn-danger float-end">Cancel</button>
                                                                                                <div className="float-end">
                                                                                            </div>
                                                                                         </div>
                                                                                    </div>
                                                                               </div>
                                                                          </div>
                                                                              :<div>
                                                                                  <UnauthorisedAccess/>
                                                                              </div> }
                    
                    
                                                                        </div>}  
                                                                                                <div>{response &&
                                                                                                response.data &&
                                                                                                response.data.data &&
                                                                                                response.data.data.length > 0 &&
                                                                                                response.data.data.map((e)=>{
                                                                                                return(
                                                                                                        <div className=" blogdivbg container">
                                                                                                                    <h4 className="title"> {e.title} </h4> 
                                                                                        
                                                                                                                    <p className="blboxbody"> {e.blog} </p>

                                                                                                            <p className="blboxfoot float-centre">Blog by:- {e.email}</p>
                                                                                                                                    
                                                                                                            </div>
                                                                                                        )
                                                                                                        })}
                                                                        
                                                                                                </div>

           
           
           
           
                                                                        
                                                                                </div>

                                                                            </div>
                                                                            

        </div>
 
    </div>
    
 </div>
   
)
}


export default Blog