import { ADD_FEEDBACK_FAIL, ADD_FEEDBACK_REQUEST, ADD_FEEDBACK_SUCCESS, GET_FEEDBACK_FAIL, GET_FEEDBACK_REQUEST, GET_FEEDBACK_SUCCESS } from "../constants/feedback"

export const GetFeedbackReducer = (state={},action)=>{

    switch(action.type){
        case GET_FEEDBACK_REQUEST:
        return {loading:true }

        case GET_FEEDBACK_SUCCESS:
            return {loading:true, response:action.payload}

        case GET_FEEDBACK_FAIL:
            return {loading:false, error:action.payload}

        default:
            return state
    }

}

export const AddFeedbackReducer = (state={},action)=>{

    switch(action.type){
        case ADD_FEEDBACK_REQUEST:
        return {loading:true }

        case ADD_FEEDBACK_SUCCESS:
            return {loading:false, response:action.payload}

        case ADD_FEEDBACK_FAIL:
            return {loading:false, error:action.payload}

        default:
            return state
    }

}


